              <svg v-if="localStorage.getItem('chatFormStatus')==1" width="14" height="14" viewBox="0 0 48 48" fill="currentColor"><path fill-rule="evenodd" clip-rule="evenodd" d="M7.003 28a1 1 0 011 1v11h11.008a1 1 0 011 1v2a1 1 0 01-1 1H6a2 2 0 01-2-2V29a1 1 0 011-1h2.003zM42 4a2 2 0 012 2v13a1 1 0 01-1 1h-2a1 1 0 01-1-1V8H29a1 1 0 01-1-1V5a1 1 0 011-1h13z" fill="currentColor"/></svg>
              <svg v-if="localStorage.getItem('chatFormStatus')==2" width="14" height="14" viewBox="0 0 48 48" fill="currentColor"><path fill-rule="evenodd" clip-rule="evenodd" d="M18 28a2 2 0 012 2v13a1 1 0 01-1 1h-2a1 1 0 01-1-1V32H5a1 1 0 01-1-1v-2a1 1 0 011-1h13zM31 4a1 1 0 011 1v11h11a1 1 0 011 1v2a1 1 0 01-1 1H30a2 2 0 01-2-2V5a1 1 0 011-1h2z" fill="currentColor"/></svg>
