          <div id="chatMessagesForm" v-if="!(data.chat.userList.length==0 || data.chat.currentUserIndexInUserList==-1)" style="width: 100%;height: 100%;overflow-y: auto;padding-top: 75px;padding-bottom: 85.8px">
