                         :style="{
                    backgroundColor:appData.theme.background
                  }"
                    >
                      <span v-if="item.type==1" style="padding: 6px 8px;display: inline-block;word-break: break-all;user-select: text"  :innerHTML="getDisplayContentHTML(item.content)"></span>
