</template>

<script>
import tool from '../utlis/tool'
import config from '../config'
import router from '../router'

export default {
  computed:{
    localStorage(){
      return localStorage
    },
    tool(){
      return tool
    },
    config(){
      return config
    }
  },
  data() {
    return {
      loading:true,
      working:false,
      type3:{
        data:{}
      },
      type4:{
        data:{}
      },
      type5:{
        data:{}
      },
      type6:{
        data:{}
      },
      type7:{
        data:{}
      },
      type8:{
        data:{}
      },
    };
  },
  methods: {
    hideChat(toPage){
      this.$emit('hideChat',toPage)
      if(toPage!=''){
        router.push(toPage)
      }
    },
    getSchoolLX(item){
      let re = ''
      if(item.is985=='是'){
        if(re!='')re+='、'
        re+='985'
      }
      if(item.is211=='是'){
        if(re!='')re+='、'
        re+='211'
      }
      if(item.isQJ=='是'){
        if(re!='')re+='、'
        re+='强基计划'
      }
      if(item.isDualClass=='是'){
        if(re!='')re+='、'
        re+='双一流'
      }
      if(re==''){
        return ''
      }else{
        return re
      }
    },
    getSchoolLocationName(item){
      if(item.city.indexOf(item.p)==-1){
        return item.p + '省' + item.city
      }else{
        return item.city
      }
    },
  },
  async mounted() {
    if(this.type==3){
      let re = await tool.axiosPostWithCheckStatus(config.serverURL,'/chat/cardInfo',{
        token:this.homeData.token,
        type:this.type,
        mark:this.data
      })
      this.type3.data = re.results
    }
    if(this.type==4){
      let re = await tool.axiosPostWithCheckStatus(config.serverURL,'/chat/cardInfo',{
        token:this.homeData.token,
        type:this.type,
        mark:this.data
      })
      this.type4.data = re.results
    }
    if(this.type==5){
      let re = await tool.axiosPostWithCheckStatus(config.serverURL,'/chat/cardInfo',{
        token:this.homeData.token,
        type:this.type,
        mark:this.data
      })
      this.type5.data = re.results
    }
    if(this.type==6){
      let re = await tool.axiosPostWithCheckStatus(config.serverURL,'/chat/cardInfo',{
        token:this.homeData.token,
        type:this.type,
        mark:this.data.userMark,
        name:this.data.name,
      })
      this.type6.data = re.results
    }
    if(this.type==7){
      let re = await tool.axiosPostWithCheckStatus(config.serverURL,'/chat/cardInfo',{
        token:this.homeData.token,
        type:this.type,
        mark:this.data
      })
      this.type7.data = re.results
    }
    if(this.type==8){
      let re = await tool.axiosPostWithCheckStatus(config.serverURL,'/chat/cardInfo',{
        token:this.homeData.token,
        type:this.type,
        mark:this.data
      })
      this.type8.data = re.results
    }
    this.loading = false
  },
  props: {
    appData:undefined,
    data:undefined,
    type:0,
    homeData:undefined,
    startChat:undefined
  }
};
</script>

